<template>
      <component :is="currentComponent"></component>
</template>
<script>
import { defineAsyncComponent } from 'vue'
export default {
  name: 'SafetyAlphabet',
  props: ['current'],
  data () {
    return {
      currentComponent: 'ip_cctv'
    }
  },
  components: {
    analog_cctv: defineAsyncComponent(() =>
      import('@/components/articles/AnalogCctv.vue')
    ),
    autonomous_fire_detector: defineAsyncComponent(() =>
      import('@/components/articles/AutonomousFireDetector.vue')
    ),
    ip_cctv: defineAsyncComponent(() =>
      import('@/components/articles/IpCctv.vue')
    ),
    lightning_protection: defineAsyncComponent(() =>
      import('@/components/articles/LightningProtection.vue')
    )
  },
  watch: {
    current (newCurrent, oldCurrent) {
      this.currentComponent = newCurrent
    }
  }
}
</script>
